CE,oCAAA;EACA,sBAAA;AA5FR;AAgGM;EA/DF;IAiEI,WAAA;IACA,YAAA;IAEA,SAAA;IACA,WAAA;EA/FN;AACF;AAmGI;EACE,kBAAA;EACA,YAAA;EACA,SAAA;EACA,2BAAA;EACA,oCAAA;EACA,YAAA;EACA,kBAAA;EACA,mBAAA;EACA,eAAA;EACA,YAAA;EACA,UAAA;EACA,oBAAA;EACA,6BAAA;AAjGN;AAmGM;EACE,UAAA;AAjGR;AAsGI;EACE,kBAAA;EACA,QAAA;EACA,SAAA;EACA,gCAAA;EA5NJ,aAAA;EACA,uBAAA;EACA,mBAAA;EA4NI,UAAA;AAlGN;AAoGM;EACE,WAAA;EACA,YAAA;EACA,kBAAA;AAlGR;AAoGQ;EACE,sBAAA;EACA,cAAA;EACA,kBAAA;EACA,WAAA;EACA,YAAA;EACA,WAAA;EACA,yBAAA;EACA,kBAAA;EACA,qEAAA;EACA,yDAAA;AAlGV;AAoGU;EACE,uBAAA;AAlGZ;AAqGU;EACE,sBAAA;AAnGZ;AAsGU;EACE,uBAAA;AApGZ;;AA4GA;EACE;IACE,uBAAA;EAzGF;EA2GA;IACE,yBAAA;EAzGF;AACF;AA4GA;EACE,aAAA;EACA,WAAA;EACA,gBAAA;EACA,0BAAA;EACA,gGAAA;AA1GF;;AA8GE;EACE,aAAA;EACA,kBAAA;AA3GJ;AA6GI;EACE,iBAAA;EACA,kBAAA;EACA,cA56BO;AAi0Bb;AA8GI;EACE,iBAAA;EACA,gCAAA;EACA,gBAAA;EACA,kBAAA;AA5GN;;AAkHA;EACE,sBAAA;EACA,2BAAA;EACA,sBAAA;EACA,WAAA;EACA,SAAA;EACA,sBAAA;EACA,kBAAA;EACA,eAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,uBAAA;AA/GF;AAkHE;EACE,qBAAA;AAhHJ;AAmHE;EACE,gBAAA;EACA,YAAA;EACA,2CAAA;AAjHJ;;AAqHA;EACE,kBAAA;EACA,QAAA;EACA,SAAA;EACA,6BAAA;EACA,WAAA;EACA,YAAA;EACA,oCAAA;EACA,kBAAA;EACA,YAAA;EAEA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,+BAAA;EAIA,eAAA;EACA,WAAA;EACA,YAAA;AAtHF;AAyHE;EAvBF;IAwBI,WAAA;IACA,YAAA;IACA,eAAA;EAtHF;AACF;AAyHE;EA9BF;IA+BI,YAAA;IACA,aAAA;IACA,eAAA;EAtHF;AACF;AAyHE;EArCF;IAsCI,YAAA;IACA,aAAA;IACA,eAAA;EAtHF;AACF;;AA2HA;EACE,WAAA;AAxHF;AA0HE;EACE,WAAA;EACA,YAAA;EACA,cAAA;AAxHJ;;AA8HA;EACE,yDAAA;EACA,eAAA;EACA,cAAA;EACA,iBAAA;EACA,+BAAA;EACA,kBAjhCc;AAs5BhB;AA6HE;EACE,gBAAA;AA3HJ","sourcesContent":["\n@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Silkscreen:wght@400;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&family=Roboto+Condensed:wght@400;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');\n\n\n// Font mixins\n@mixin roboto-regular {\n  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n}\n\n@mixin roboto-condensed {\n  font-family: 'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n}\n\n@mixin roboto-light {\n  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 300;\n  -webkit-font-smoothing: antialiased;\n}\n\n\n@mixin robotomono-light {\n  font-family: 'RobotoMono', -apple-system, monospace;\n  font-weight: 300;\n  -webkit-font-smoothing: none;\n}\n\n@mixin silkscreen {\n  font-family: Silkscreen, -apple-system, monospace;\n  font-weight: 300;\n  -webkit-font-smoothing: none;\n}\n\n// Font imports\n@font-face {\n  font-family: 'alagard';\n  src: url('../fonts/alagard.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'monaco';\n  src: url('../fonts/monaco.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n\n@font-face {\n  font-family: 'RobotoMono';\n  src: url('../fonts/RobotoMono-Light.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n\n}\n/*\n  Source Sans Pro: Best for readability in dense information sections, with a clean, modern look that works well at smaller sizes.\n  Barlow: Has a slightly technical feel that matches strategy games, with excellent readability and character distinction.\n  Open Sans: Superior readability across all screen sizes and devices, with a neutral but professional appearance.\n  Rajdhani & Oxanium: Added for headings - they have a technical/sci-fi feel that complements strategy games while remaining readable.\n */\n\n\n// Import statements for recommended body text fonts\n@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Barlow:wght@400;500;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@500;600;700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Oxanium:wght@400;500;600&display=swap');\n\n// Body text font mixins\n@mixin source-sans {\n  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin source-sans-semibold {\n  font-family: 'Source Sans Pro', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 600;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin barlow {\n  font-family: 'Barlow', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin barlow-medium {\n  font-family: 'Barlow', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 500;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n@mixin open-sans {\n  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.02em;\n}\n\n// Heading font mixins\n@mixin rajdhani-bold {\n  font-family: 'Rajdhani', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 700;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.03em;\n}\n\n@mixin oxanium {\n  font-family: 'Oxanium', -apple-system, BlinkMacSystemFont, sans-serif;\n  font-weight: 500;\n  -webkit-font-smoothing: antialiased;\n  letter-spacing: 0.01em;\n}\n\n// TODO: https://stackoverflow.com/questions/12562631/web-fonts-and-providing-fallback-fonts\n","@use \"sass:color\";\n@use 'sass:math' as math;\n@use 'fonts' as *;\n@use \"sass:map\" as map;\n@use \"sass:list\" as list;\n\n// color palette\n// https://lospec.com/palette-list/comfy52\n\n// Variables\n$header-font: 'Silkscreen', 'Press Start 2P', monospace;\n$description-font:  'Roboto Condensed', -apple-system, BlinkMacSystemFont, sans-serif;\n$accent-color:  #C2D64F ;\n\n$text-light: #FFFFFF;\n$card-bg: rgba(0, 0, 0, 0.45);\n$border-radius: 8px;\n$page-bg: #2b222a;\n\n\n\n// Base styles\nhtml {\n  font-size: 16px;\n  min-width: 100vw;\n  margin: 0;\n  padding: 0;\n  background-color: $page-bg;\n\n  text-rendering: optimizeLegibility;\n  -webkit-font-smoothing: antialiased;\n  overflow-x: hidden; /* Prevent horizontal scrolling */\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  text-align: center;\n\n  margin: 0;\n  padding: 0;\n  font-size: clamp(2rem, 5vw, 4rem);\n  line-height: 1.0;\n\n  @include source-sans-semibold;\n\n  font-weight: normal;\n  color: $text-light;\n  margin: 0px auto;\n  width: 100%;\n  max-width: 100vw;\n\n  letter-spacing: 0rem;\n  overflow-x: hidden; /* Hide horizontal overflow */\n  box-sizing: border-box; /* Include padding in width calculation */\n}\n\n//\nhtml, body {\n  // Prevent viewport height changes from affecting layout\n  //height: 100%;\n  overflow-x: hidden;\n\n}\n\n// Typography\nh1, h2, h3, h4, h5, h6 {\n  line-height: 1;\n  text-rendering: geometricPrecision;\n  font-smooth: never;\n  -webkit-font-smoothing: none;\n  margin-top: 0;\n  margin-bottom: 2rem;\n  font-weight: normal;\n  color: $text-light;\n  letter-spacing: 0rem;\n\n}\n\nh1, h2 {\n  @include rajdhani-bold;\n  font-size: 64px;\n  line-height: 1.6;\n  color: #f0f0f0;\n\n  // Create appropriate spacing consistent with your design\n  margin: 0.25em 0;\n\n  // Slightly increase letter-spacing for better readability on dark backgrounds\n  letter-spacing: 0.2px;\n\n  @media (max-width: 768px) {\n    font-size: 32px;\n  }\n}\n\nh3, h4, h5, h6 {\n  //@include rajdhani-bold;\n  @include source-sans-semibold;\n  //text-align: left;\n  color: #f0f0f0;\n  max-width: 70ch; // Maximum readable line length\n\n  // Fluid typography\n  font-size: clamp(1.125rem, 2vw + 0.5rem, 2rem); // Smooth scaling\n  line-height: clamp(1.3, 1.3px + (0.3 * (1vw / 10)), 1.6); // Fluid line height\n  letter-spacing: 0.01em;\n\n  // Fluid margins - more whitespace on larger screens\n  margin: clamp(1rem, 5vw, 3rem) 0; // 16px to 48px margins\n\n  &:is(h3) {\n    font-size: clamp(1.75rem, 3.0vw + 0.5rem, 2.5rem);\n  }\n\n  // Specific h4/h5/h6 adjustments - maintain hierarchy with fluid sizes\n  &:is(h4) {\n    font-size: clamp(1.25rem, 2.5vw + 0.5rem, 2.25rem);\n  }\n\n  &:is(h5) {\n    font-size: clamp(1.125rem, 2vw + 0.5rem, 2rem);\n  }\n\n  &:is(h6) {\n    font-size: clamp(1rem, 1.5vw + 0.5rem, 1.75rem);\n  }\n\n  // Base breakpoints styles\n  @media (max-width: 768px) {\n    // Content remains the same, just scaled appropriately\n    // Minimum whitespace on small screens\n    margin: 1rem 0;\n  }\n\n  // Tablet/Medium devices\n  @media (min-width: 769px) and (max-width: 1500px) {\n    // Medium whitespace\n    margin: 1.5rem 0;\n  }\n\n  // Desktop/Large devices\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    // More generous whitespace\n    margin: 2rem 0;\n  }\n\n  // Extra large devices\n  @media (min-width: 2001px) {\n    // Maximum whitespace\n    margin: 3rem 0;\n  }\n}\n\n//\n//h1 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h2 { font-size: 6rem; line-height: 1.0; letter-spacing: 0rem; }\n//h3 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h4 { font-size: 5rem; line-height: 1.0; letter-spacing: 0rem; }\n//h5 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n//h6 { font-size: 3rem; line-height: 1.0; letter-spacing: 0rem; }\n\n\n\n\nheader {\n  margin-bottom: 25px;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  box-sizing: border-box;\n  overflow: hidden;\n}\nfooter {\n  background: $card-bg;\n  margin-bottom: 25px;\n  width: 100%;\n  max-width: 100vw;\n  box-sizing: border-box;\n  overflow: hidden;\n}\n\n// Links\na {\n\n  font-weight: normal;\n  text-decoration: none;\n  @include silkscreen;\n  color: $accent-color;\n\n  &:focus {\n    outline: thin dotted;\n    color: color.adjust($accent-color, $lightness: 10%);\n  }\n}\n\n// Marketing section styling\n.marketing-content {\n  //background: $card-bg;\n  padding: 25px;\n  margin-bottom: 25px;\n  border-radius: $border-radius;\n\n  margin-left: auto;\n  margin-right: auto;\n\n  // Ensure minimum width for readability\n  min-width: min(300px, 90vw);\n\n  &__slogan {\n    color: $accent-color;\n  }\n\n  // Tablet/Medium devices\n  @media (min-width: 764px) and (max-width: 1500px) {\n    max-width: 70vw;\n  }\n\n  // Desktop/Large devices\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    max-width: 50vw;\n  }\n\n  // Extra Large devices\n  @media (min-width: 2001px) {\n    max-width: 35vw;\n    // Add a max-width in pixels to prevent excessive width on ultra-wide screens\n    max-width: clamp(35vw, 35vw, 1200px);\n  }\n}\n\n// Features Component - BEM Architecture\n// Fortifend Website\n\n.features {\n  width: 100vw;\n  margin: 40px auto;\n  justify-content: center;\n  display: flex;\n  flex-direction: column;\n\n  // Tablet/Medium\n  @media (min-width: 769px) and (max-width: 1500px) {\n    width: 80vw;\n  }\n\n  // Desktop/Large\n  @media (min-width: 1501px) and (max-width: 2000px) {\n    width: 75vw;\n  }\n\n  @media (min-width: 2001px) {\n    width: 50vw;\n  }\n\n  // List container\n  &__list {\n    display: grid;\n    //gap: 10vw;\n    gap: 48px;\n    margin: 0;\n    padding: 0;\n    //align-content: center;\n    //align-items: center;\n    horiz-align: center;\n    //align: center;\n    list-style: none;\n\n    // Mobile - single column\n    grid-template-columns: 1fr;\n\n    // Tablet/Medium\n    @media (min-width: 769px) and (max-width: 1500px) {\n      grid-template-columns: repeat(2, 1fr);\n    }\n\n    // Desktop/Large\n